import { Button, Form, Input } from 'antd';
import { forgotPassWord } from 'api/forgotPassword';
import React from 'react';
import { useHistory } from 'react-router-dom';
import styles from './style.module.scss';
import lock from 'assets/forgotPassword/lock.png';
import { handleErrorMessage } from 'helper';

interface ForgotProps {
  email: string;
}
function Forgot({ email }: ForgotProps) {
  const history = useHistory();
  const handleSubmit = async (payload: any) => {
    const password = {
      newPassword: payload.newPassword,
      email: email,
    };
    try {
      await forgotPassWord(password);
      history.push('/login');
    } catch (error) {
      handleErrorMessage(error);
    }
  };
  return (
    <div className={styles.forgotContainer}>
      <div className={styles.iconContainer}>
        <div className={styles.icon}>
          <img src={lock} alt="" />
        </div>
      </div>
      <h3>Tạo mật khẩu mới</h3>
      <Form onFinish={handleSubmit}>
        <Form.Item
          name="newPassword"
          rules={[
            {
              required: true,
            },
          ]}
          wrapperCol={{ span: 24 }}
        >
          <Input type="password" placeholder="New password" className={styles.inputCustom} maxLength={50} />
        </Form.Item>

        <Form.Item
          name="confirm"
          rules={[
            {
              required: true,
            },
          ]}
          wrapperCol={{ span: 24 }}
        >
          <Input type="password" placeholder="Confirm password" className={styles.inputCustom} maxLength={50} />
        </Form.Item>
        <Button htmlType="submit" className={styles.submit}>
          Gửi
        </Button>
      </Form>
      <div className={styles.back} onClick={() => history.push('/login')}>
        <p>Hủy</p>
      </div>
    </div>
  );
}

export default Forgot;
